@import "../utils";

$opacity: 0.6;

.confirmation {
  display: grid;
  justify-items: center;
  grid-gap: 20px;
  margin-top: 55px;
  &__title {
    text-align: center;
    text-transform: inherit;
    font-size: 48px;
  }
  &__link {
    text-transform: uppercase;
    text-decoration: underline;
    margin-top: 40px;
  }
}

@media only screen and (max-width: 768px) {
  .confirmation__title {
    max-width: 300px;
    width: 100%;
    font-size: 24px;
  }
}

@media only screen and (max-width: 400px) {
  .confirmation__link {
    margin-top: 120px;
  }
}
